<script>
import $ from 'jquery'
import 'jquery.nicescroll'
import Swiper from 'swiper';
import "swiper/dist/css/swiper.min.css";
import { setTimeout, setInterval } from 'timers';
export default {
    name: "home",
    props:['ishome'],
    data() {
        return{
            loadding: 0,
            paginationShow: false,
            itemContentSelectLeft: 1,
            itemContentSelectRight: 1,
            timerSelectRight: 0,
            isRollup: true,
            friendListSelect: false,
            gameSelect: 0,
            gameList:[
                {
                    'name': '',
                    'Icon': require("@/pictures/game/01.jpg"),
                },
                {
                    'name': 'Apex',
                    'Icon': require("@/pictures/game/02.jpg"),
                },
                {
                    'name': '',
                    'Icon': require("@/pictures/game/03.jpg"),
                },
                {
                    'name': '',
                    'Icon': require("@/pictures/game/04.jpg"),
                },
            ],
            gameStatus:[
                {
                    'status': 3
                },
                {
                    'status': 4
                },
                {
                    'status': 5
                },
                {
                    'status': 5
                },
            ],
            messageList: [
                {
                    'name': 'wangyi1',
                    'Icon': require("@/pictures/icon/message/01.jpg"),
                    'time': '12:56',
                    'content': '',
                    'new': 1,
                },
                {
                    'name': 'wangyi2',
                    'Icon': require("@/pictures/icon/message/02.jpg"),
                    'time': '13:00',
                    'content': '',
                    'new': 2,
                },
                {
                    'name': 'wangyi3',
                    'Icon': require("@/pictures/icon/message/03.jpg"),
                    'time': '13:05',
                    'content': '',
                    'new': 3,
                },
                {
                    'name': 'wangyi4',
                    'Icon': require("@/pictures/icon/message/04.jpg"),
                    'time': '13:10',
                    'content': '',
                    'new': 4,
                },
                {
                    'name': 'wangyi5',
                    'Icon': require("@/pictures/icon/message/05.jpg"),
                    'time': '13:20',
                    'content': '',
                    'new': 5,
                },
                {
                    'name': 'wangyi6',
                    'Icon': require("@/pictures/icon/message/06.jpg"),
                    'time': '13:30',
                    'content': '',
                    'new': 6,
                },
                // {'name': 'wangyi7'},
                // {'name': 'wangyi8'},
                // {'name': 'wangyi9'},
                // {'name': 'wangyi10'},
                // {'name': 'wangyi11'},
                // {'name': 'wangyi12'},
            ],
            friendList:[
                {
                    'name': 'wangyi1',
                    'Icon': require("@/pictures/icon/message/01.jpg"),
                },
                {
                    'name': 'wangyi2',
                    'Icon': require("@/pictures/icon/message/02.jpg"),
                },
                {
                    'name': 'wangyi3',
                    'Icon': require("@/pictures/icon/message/03.jpg"),
                },
                {
                    'name': 'wangyi4',
                    'Icon': require("@/pictures/icon/message/04.jpg"),
                },
                {
                    'name': 'wangyi5',
                    'Icon': require("@/pictures/icon/message/05.jpg"),
                },
                {
                    'name': 'wangyi6',
                    'Icon': require("@/pictures/icon/message/06.jpg"),
                },
                // {'name': 'wangyi7'},
                // {'name': 'wangyi8'},
                // {'name': 'wangyi9'},
                // {'name': 'wangyi10'},
                // {'name': 'wangyi11'},
                // {'name': 'wangyi12'},
            ],
            topics:[
                {
                    'topic': ''
                },
                {
                    'topic': '8.03'
                },
                {
                    'topic': ' '
                },
                {
                    'topic': 'EVE9'
                },
                {
                    'topic': ''
                },
            ],
            ops: {
                vuescroll: {
                    mode: 'native',// 1:native 2:slide
                    sizeStrategy: 'nummber',// 1:number  2:percent
                    detectResize: true
                },
                scrollPanel: {
                    initialScrollY: false,
                    initialScrollX: false,
                    scrollingX: false,
                    scrollingY: true,
                    speed: 300,
                    easing: undefined,
                    verticalNativeBarPos: 'right'
                },
                rail: {},
                bar: {}
            }
        }
    },
    // watch:{
    //     "ishome": function(){
    //         var _this = this;
    //         if(this.ishome === 4){
    //             _this.swiper_init();
    //         }
    //     }
    // },
    watch: {
      "itemContentSelectRight": function(){
          var _this = this;
          if((this.itemContentSelectRight === 2)&&(this.friendListSelect === false)){
            setTimeout(function(){
                if(_this.timerSelectRight === 3) _this.itemContentSelectRight = 1;
            },3000);
          }else{
            _this.timerSelectRight = 0;
          }
      },
      "friendListSelect": function(){
          var _this = this;
          if((this.itemContentSelectRight === 2)&&(this.friendListSelect === false)){
            setTimeout(function(){
                if(_this.timerSelectRight === 3) _this.itemContentSelectRight = 1;
            },3000);
          }else{
            _this.timerSelectRight = 0;
          }
      }
    },
    mounted(){
        var _this = this;
        this.preload(100);
        let timer = setInterval(
            function(){
                if((_this.itemContentSelectRight === 2)&&(_this.friendListSelect === false)) _this.timerSelectRight += 1;
            },1000
        );
    },
    methods:{
        preload: function(e){
            var _this = this;
            this.loadding += e;
            if(this.loadding === 100){
                _this.$emit('homeLoadding', true);
            }
        },
        itemSelectLeft: function(e){
            this.itemContentSelectLeft = e;
        },
        itemSelectRight: function(e){
            var _this = this;
            if((this.itemContentSelectRight === 2)&&(e === 2)){
                _this.itemContentSelectRight = 1;
            }else{
                _this.itemContentSelectRight = e;
            }
        },
        friendListEnter: function(){
            this.friendListSelect = true;
            this.timerSelectRight = 0;
        },
        friendListLeave: function(){
            this.friendListSelect = false;
        },
        swiper_init: function(){
            var swiperContianer = new Swiper('.swiper-container', {
                    loop: true,        
                    direction: 'horizontal', //
                    effect: 'slide', //
                    observer: true,
                    observerParents: true,
                    observeSlideChildren: true,
                    pagination: {
                        el: '.swiper-pagination',
                        clickable :true,
                        renderBullet: function (index, className) {
			                return  '<span class="'+className+'"><img src="static/img/0' + (index + 1) + '.jpg"></span>';
		    	        },
                    },
                    // navigation: {
                    //     nextEl: '.swiper-button-next',
                    //     prevEl: '.swiper-button-prev',
                    // },
                    autoplay: true,
                    autoplay:{
                        disableOnInteraction: false,
                        delay: 3000,
                    },
                });
            swiperContianer.pagination.render();
            swiperContianer.autoplay.start();
        },
        swiperSelect: function(){
            this.paginationShow = true;
        },
        swiperSelectOut: function(){
            this.paginationShow = false;
        },
        listRollup: function(e){
            var _this = this;
            if(e === 1){
                _this.isRollup = !(_this.isRollup);
            }else{
                _this.isRollup = true;
            }
        },
        gameScroll: function(event){
            var _this = this;
            console.log(_this.gameSelect);
            if(event.deltaY > 0){
                if(_this.gameSelect < _this.gameList.length - 1)
                {
                    if(_this.gameSelect > 0){
                        _this.$set(_this.gameStatus[_this.gameSelect - 1], 'status', _this.gameStatus[_this.gameSelect - 1].status - 1);
                    }
                    _this.$set(_this.gameStatus[_this.gameSelect], 'status', _this.gameStatus[_this.gameSelect].status - 1);
                    _this.$set(_this.gameStatus[_this.gameSelect + 1], 'status', _this.gameStatus[_this.gameSelect + 1].status - 1);
                    if(_this.gameSelect < _this.gameList.length - 2){
                        _this.$set(_this.gameStatus[_this.gameSelect + 2], 'status', _this.gameStatus[_this.gameSelect + 2].status - 1);
                    }
                    if(_this.gameSelect < _this.gameList.length - 1){_this.gameSelect += 1;}
                }
            }else if(event.deltaY < 0){
                if(_this.gameSelect > 0){
                    if(_this.gameSelect > 1){
                        _this.$set(_this.gameStatus[_this.gameSelect - 2], 'status', _this.gameStatus[_this.gameSelect - 2].status + 1);
                    }
                    _this.$set(_this.gameStatus[_this.gameSelect - 1], 'status', _this.gameStatus[_this.gameSelect - 1].status + 1);
                    _this.$set(_this.gameStatus[_this.gameSelect], 'status', _this.gameStatus[_this.gameSelect].status + 1);
                    if(_this.gameSelect < _this.gameList.length - 1){
                        _this.$set(_this.gameStatus[_this.gameSelect + 1], 'status', _this.gameStatus[_this.gameSelect + 1].status + 1)
                    }
                }
                if(_this.gameSelect > 0){_this.gameSelect -= 1;}
            }
            console.log(_this.gameStatus);
        }
    }    
}
</script>
