    top: 0;
    width: 100%;
    height: 70%;
    min-height: 385px;
    border-radius: 5px;
    background-color:rgba(0, 0, 0, 0.8);
}

.swiper-pagination{
    position: absolute;
    left: 50%;
    bottom: -2px;
    margin-left: -50%;
    width: 100%;
    height: 30%;
    min-height: 100px;
    border-radius: 0 0 5px 5px;
    background-color: rgba(200, 200, 200, 0.6);
}

.swiper-pagination-bullet{
    width:auto !important;
    height: 100%!important;
    min-height: 100px!important;
    margin-left: 2px;
    margin-right: 2px;
    background:none !important;
    cursor:pointer;
}

.swiper-pagination-bullet img{
    width:auto;
    height: 100%!important;
    min-height: 100px!important;
    cursor:pointer;
}

.swiper-slide {
    border-radius: 5px;
    text-align: center;
    font-size: 18px;
    background: #fff;
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.swiper-slide img {
    max-width: 100%;
    height: auto;
} 

.buttons{
    position: absolute;
    left: 50%;
    bottom: 3%;
    margin-left: -49%;
    width: 98%;
    height: 20%;
    min-height: 110px;
    font-size: 15px;
    border-radius: 5px;
    border: 1px solid rgba(0, 0, 0, 0.2);
}

.buttonArea{
    display: inline-block;
    width: 33%;
    height: 100%;
    line-height: 100%;
}

.homeButton{
    margin-left: 10%;
    width: 80%;
    height: 100%;
}

.filter{
  z-index: 100;
  position: absolute;
  border-radius: 50%;
  background-color: rgba(75, 75, 75, 0.5);
}

::-webkit-scrollbar  
{  
    width: 4px;  
    height: 4px;  
    background-color: #F5F5F5;  
}

::-webkit-scrollbar-track  
{  
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);  
    border-radius: 10px;  
    background-color: #FFF;  
}

::-webkit-scrollbar-thumb  
{  
    border-radius: 10px;  
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
    background-color: #AAA;  
} 

/* ::-webkit-scrollbar {display:none} */

</style>



