<template>
    <div>
        <a-alert :message = broName banner />
    </div>
</template>
<script>
export default {
    name: 'Check',
    data(){
        return {
            broName : 'Runing',
            is360 : false,
            isIE : false,
            isFirefox : false,
            isChrome : false,
            isEdge : false,
            select : 2
        }
    },
    mounted: function(){
        var _this = this;
        this.init();
        this.CallBack();
    },
    methods:{
        init: function(){
            var _this = this;
            var str = '';
            var strStart = 0;
            var strStop = 0;
            var arr = new Array();
            var temp = '';
            var tip = '';

            var userAgent = window.navigator.userAgent; //appCodeName,appName,appVersion,language,platform

            /*alert(userAgent);*/

            //FireFox
            if (userAgent.indexOf('Firefox') != -1) {
                _this.isFireFox = true;
                /*broName = 'FireFox';*/
                strStart = userAgent.indexOf('Firefox');
                temp = userAgent.substring(strStart);
                _this.broName = temp.replace('/', '')
                _this.select = 3;
            }

            //Edge
            if (userAgent.indexOf('Edge') != -1) {
                _this.isEdge = true;
                /*broName = 'Edge';*/
                strStart = userAgent.indexOf('Edge');
                temp = userAgent.substring(strStart);
                _this.broName = temp.replace('/', '');
                tip = ' ' + _this.broName + ' ,  Chrome ';
                _this.broName = tip;
                _this.select = 5;
            }

            //IE
            if (userAgent.indexOf('NET') != -1 && userAgent.indexOf("rv") != -1) {
                _this.isIE = true;
                /*broName = 'IE'; */
                strStart = userAgent.indexOf('rv');
                strStop = userAgent.indexOf(')');
                temp = userAgent.substring(strStart, strStop);
                _this.broName = temp.replace('rv', 'IE').replace(':', '');
                tip = ' ' + _this.broName + ' ,  Chrome ';
                _this.broName = tip;
                _this.select = 2;
            }

            //360360360
            if (userAgent.indexOf('WOW') != -1 && userAgent.indexOf("NET") < 0 && userAgent.indexOf("Firefox") < 0) {
                if(navigator.javaEnabled()){
                    _this.is360 = true;
                    _this.broName = '360-';
                }else{
                    _this.is360 = true;
                    _this.broName = '360-';
                } 
                _this.select = 1;
            }

            //360
            if (userAgent.indexOf('WOW') != -1 && userAgent.indexOf("NET") != -1 && userAgent.indexOf("MSIE") != -1 && userAgent.indexOf("rv") < 0) {
                _this.is360 = true;
                _this.broName = '360';
                _this.select = 1;
            }

            //Chrome
            if (userAgent.indexOf('WOW') < 0 && userAgent.indexOf("Edge") < 0) {
                _this.isChrome = true;
                /*broName = 'Chrome';*/
                strStart = userAgent.indexOf('Chrome');
                strStop = userAgent.indexOf('Safari');
                temp = userAgent.substring(strStart, strStop);
                _this.broName = temp.replace('/', '');
                _this.select = 4;
            }
        },
        CallBack: function(){
            if((this.select === 1)||(this.select === 3)||(this.select === 4))
                this.$emit("callBack", false)
            else
                this.$emit("callBack", true)
        }
    }
}
</script>
<style>

</style>



